    fetch("/marcas")
      .then(res => res.json())
      .then(({ res }) => setBrands(res))

    fetch("/modelos")
      .then(res => res.json())
      .then(({ res }) => setModels(res))

    fetch("/fallas")
      .then(res => res.json())
      .then(({ res }) => setFaults(res))

    fetch("/soluciones")
      .then(res => res.json())
      // .then( res => console.log(res))
      .then(({ res }) => setSolutions(res))

    fetch("/msd")
      .then(res => res.json())
      .then(({ res }) => setMsd(res))

    fetch("/chip")
      .then(res => res.json())
      .then(({ res }) => setChip(res))

    fetch("/clientes")
      .then(res => res.json())
      .then(({ res }) => setClients(res))

