import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { isEmpty } from "utils/utils";

export default function CardClientNew() {

  const history = useHistory();
  const [newClient, setNewClient] = useState({});
  const clientToUpdate = history.location.state?.client;

  useEffect(() => {
    setNewClient({...clientToUpdate})
  }, [clientToUpdate]);

  const onChangeForm = (e) => {
    e.preventDefault()

    setNewClient({
      ...newClient,
      [e.target.name]: e.target.value
    })
  }

  const onCreateClient = (e) => {

    e.preventDefault();

    if (!isEmpty(newClient)) {

      if (clientToUpdate) {
        
        fetch(`/clientes/${clientToUpdate?.id}`, {
          method: "PUT",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newClient)
        })
          .then(res => res.json())
          .then(res => console.log(res))

        history.goBack()
        
      } else {

        fetch("/clientes", {
          method: "POST",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newClient)
        })
          .then(res => res.json())
          .then(res => console.log(res))

        history.goBack()

      }

    } else {
      console.log("No")
      // Agregar alg√∫n tipo de Toast para que recuerde que los campos no deben estar vac√≠os
    }

  }

return (
  <>
    <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-200 border-0">
      <div className="rounded-t bg-white mb-0 px-6 py-6">
        <div className="text-center flex justify-between">
          <h6 className="text-gray-800 text-xl font-bold">{clientToUpdate ? "Editar" : "Nuevo"} Cliente</h6>
        </div>
      </div>
      <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
        <form onChange={onChangeForm} onSubmit={onCreateClient}>
          <div className="flex flex-wrap mt-6">
            <div className="w-full lg:w-4/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Nombre
                  </label>
                <input
                  type="text"
                  name="nombre"
                  autoComplete="off"
                  value={newClient?.nombre}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                />
              </div>
            </div>
            <div className="w-full lg:w-4/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Apellido Paterno
                  </label>
                <input
                  type="text"
                  name="apellido_paterno"
                  autoComplete="off"
                  value={newClient?.apellido_paterno}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                />
              </div>
            </div>
            <div className="w-full lg:w-4/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Apellido Materno
                  </label>
                <input
                  type="text"
                  name="apellido_materno"
                  autoComplete="off"
                  value={newClient?.apellido_materno}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                />
              </div>
            </div>
            <div className="w-full lg:w-3/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Telefono 1
                  </label>
                <input
                  type="tel"
                  name="telefono1"
                  autoComplete="off"
                  value={newClient?.telefono1}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                />
              </div>
            </div>
            <div className="w-full lg:w-3/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Telefono 2
                  </label>
                <input
                  type="tel"
                  name="telefono2"
                  value={newClient?.telefono2}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                />
              </div>
            </div>
            <div className="w-full lg:w-6/12 px-4">
              <div className="relative w-full mb-3">
                <label
                  className="block uppercase text-gray-700 text-xs font-bold mb-2"
                  htmlFor="grid-password"
                >
                  Notas
                  </label>
                <textarea
                  type="text"
                  name="notas"
                  autoComplete="off"
                  value={newClient?.notas}
                  className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  rows="1"
                ></textarea>
              </div>
            </div>
          </div>
          <div className="flex flex-wrap mt-6 justify-end">
            <button
              className="bg-blue-500 text-white active:bg-blue-600 font-bold uppercase text-xs px-4 py-2 rounded shadow hover:shadow-md outline-none focus:outline-none mr-1 ease-linear transition-all duration-150"
              type="submit"
            >
              Guardar
                </button>
          </div>
        </form>
      </div>
    </div>
  </>
);
}
