import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { isEmpty } from "utils/utils";

// components

export default function CardSolutionNew() {

  const history = useHistory();
  const [faults, setFaults] = useState([]);
  const [newSolution, setNewSolution] = useState({});

  const solutionToUpdate = history.location.state?.itemToUpdate;


  useEffect(() => {
    setNewSolution({...solutionToUpdate})
  }, [solutionToUpdate]);


  const onChangeForm = (e) => {
    e.preventDefault();

    e.target.name === "falla_id"
    ? setNewSolution({
        ...newSolution,
        [e.target.name]: parseInt(e.target.value)
      })
    : setNewSolution({
      ...newSolution,
      [e.target.name]: e.target.value
    })
  }

  const onCreateSolution = (e) => {
    e.preventDefault();

    if(!isEmpty(newSolution)) {
      
      if (solutionToUpdate) {

        fetch(`/soluciones/${solutionToUpdate?.id}`, {
          method: "PUT",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newSolution)
        })
          .then( res => res.json() )
          .then( res => console.log(res))
  
        history.goBack()

      } else {

        fetch("/soluciones", {
          method: "POST",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newSolution)
        })
          .catch( err => console.log(err))
          .then( res => res.json() )
          .then( res => {
            console.log(res)
            history.goBack()
        })

      }   
      
    } else {
      
      console.log("No")

    }
  }

  useEffect(() => {

    fetch("/fallas")
      .then( res => res.json() )
      .then( ({ res }) => setFaults(res) )

  }, [])

  return (
    <>
      <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-200 border-0">
        <div className="rounded-t bg-white mb-0 px-6 py-6">
          <div className="text-center flex justify-between">
            <h6 className="text-gray-800 text-xl font-bold">Nueva Solucion</h6>
          </div>
        </div>
        <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
          <form onChange={onChangeForm} onSubmit={onCreateSolution}>
            <div className="flex flex-wrap mt-6">
              <div className="w-full px-4">
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Falla
                  </label>
                  <select name="falla_id" value={newSolution?.falla_id} className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150">
                    <option selected value={null}></option>
                    {
                      faults.map( ({id, falla}) => {
                        return <option key={falla} value={id}>{falla}</option>
                      })
                    }
                  </select>
                </div>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Solucion
                  </label>
                  <input
                    type="text"
                    name="solucion"
                    autoComplete="off"
                    value={newSolution?.solucion}
                    className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  />
                </div>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Descripcion de Solucion
                  </label>
                  <input
                    type="text"
                    name="descripcion"
                    autoComplete="off"
                    value={newSolution?.descripcion}
                    className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  />
                </div>
              </div>
            </div>
            <div className="flex flex-wrap mt-6 justify-end">
            <button
              className="bg-blue-500 text-white active:bg-blue-600 font-bold uppercase text-xs px-4 py-2 rounded shadow hover:shadow-md outline-none focus:outline-none mr-1 ease-linear transition-all duration-150"
              type="submit"
            >
              Guardar
            </button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}
