import React, {useState, useEffect} from "react";
import { useHistory } from "react-router-dom";
import { isEmpty } from "utils/utils";

// components

export default function CardModelNew() {

  const history = useHistory()
  const [brands, setBrands] = useState([]);
  const [newModel, setNewModel] = useState({});
  const modelToUpdate = history.location.state?.itemToUpdate;

  useEffect(() => {
    
    fetch("http://localhost:8080/marcas")
      .then( res => res.json() )
      .then( ({ res }) => { 
        setBrands(res)
      })

  }, [])

  useEffect(() => {
    setNewModel({...modelToUpdate})
  }, [modelToUpdate])

  const onChangeForm = (e) => {
    e.preventDefault()

    e.target.name === "marca_id"
    ? setNewModel({
        ...newModel,
        [e.target.name]: parseInt(e.target.value)
      })
    : setNewModel({
      ...newModel,
      [e.target.name]: e.target.value
    })
  }

  const onCreateModel = (e) => {

    e.preventDefault();


    if (!isEmpty(newModel)) {

      if (modelToUpdate) {

        fetch(`/modelos/${modelToUpdate?.id}`, {
          method: "PUT",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newModel)
          })
          .then(res => res.json())
          .then(res => {
            history.goBack() 
            console.log(res)
          })

      } else {

        fetch("/modelos", {
          method: "POST",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newModel)
        })
          .then(res => res.json())
          .then(res => {
            history.goBack()
            console.log(res)
          })

      }

      

    } else {
      
      console.log("No")

      // Agregar alg√∫n tipo de Toast para que recuerde que los campos no deben estar vac√≠os

    }

  }

  return (
    <>
      <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-200 border-0">
        <div className="rounded-t bg-white mb-0 px-6 py-6">
          <div className="text-center flex justify-between">
            <h6 className="text-gray-800 text-xl font-bold">{modelToUpdate ? "Editar" : "Nuevo"} Modelo</h6>
          </div>
        </div>
        <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
          <form onChange={onChangeForm} onSubmit={onCreateModel}>
            <div className="flex flex-wrap mt-6">
              <div className="w-full px-4">
                <div className="relative w-full mb-3">
                  
                  <select name="marca_id" value={newModel?.marca_id} className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150">
                    <option selected value={null}></option>
                    {
                      brands.map( ({ marca, id }) => {
                        return <option key={marca} value={id}>{ marca }</option>
                      })
                    }
                  </select>
                </div>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Modelo
                  </label>
                  <input
                    type="text"
                    name="modelo"
                    autoComplete="off"
                    value={newModel?.modelo}
                    className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  />
                </div>
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Modelo Numerico
                  </label>
                  <input
                    type="text"
                    name="modelo_num"
                    autoComplete="off"
                    value={newModel?.modelo_num}
                    className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  />
                </div>
              </div>
            </div>
            <div className="flex flex-wrap mt-6 justify-end">
              <button
                className="bg-blue-500 text-white active:bg-blue-600 font-bold uppercase text-xs px-4 py-2 rounded shadow hover:shadow-md outline-none focus:outline-none mr-1 ease-linear transition-all duration-150"
                type="submit"
              >
                Guardar
            </button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}
